import React, { useEffect } from 'react';
import Aos from "aos";
import Me from '../image/img2.jpg';
import Background from '../image/about.png';
import '../css/Intro.css';
import "../css/About.css";

const About = () => {
	useEffect(() => {
		Aos.init({duration: 2000});
	}, [])
	const [imageLoaded1, setImageLoaded1] = React.useState(false);
	const setImage1True = React.useCallback(() => setImageLoaded1(true),[],);
	const [imageLoaded2, setImageLoaded2] = React.useState(false);
	const setImage2True = React.useCallback(() => setImageLoaded2(true),[],);
	const [personal, setPersonal] = React.useState(false);
	const updatePersonal = (event) => {setPersonal(event);};
	const image1 = () => (
		<img
		alt='profile'
		onLoad={setImage1True}
		className= "noSelect"
		style={{
			marginLeft: '1vw',
			width: '20vw',
			maxWidth:'400px',
			height: '20vw',
			maxHeight:'400px',
			borderRadius: '50%',
			objectFit: 'cover'
		}}
		src={Me}
		/>
	)
	const image2 = () => (
		<img
		alt='profile'
		onLoad={setImage2True}
		className= "noSelect"
		style={{
			position: 'absolute',
			objectFit: 'cover',
			width: '100%',
			bottom: '0',
		}}
		src={Background}
		/>
	)
	if(!(imageLoaded1 && imageLoaded2)) {return (
		<div
		style= {{
			width: '100%',
			height: '100%',
			backgroundColor: '#1A1C1E',
			position: 'absolute',
			display: 'flex',
			alignItems: 'center',
			justifyContent: 'center',
			flexDirection: 'column',
		}}
		>
			<div className='loader'>
				<span></span>
				<span></span>
				<span></span>
			</div>
			<div hidden={true}>{image1()}</div>
			<div hidden={true}>{image2()}</div>
		</div>
	)}
	let delay = 1000;
	const bio1 = () => (
		<div>
			<div data-aos="fade-left" data-aos-easing="ease-out-sine" data-aos-duration={delay+600}>
				Major GPA: 3.68 | Cumulative GPA: 3.54
			</div>
			<div style={{marginTop:'.5em'}} data-aos="fade-left" data-aos-easing="ease-out-sine" data-aos-duration={delay+300}>
				Senior at Indiana University Bloomington pursuinga B.S. in Computer Science.<br/>
				Specializing in Software Engineering and graduating in May 2021.
			</div>
			<div style={{marginTop:'.5em'}} data-aos="fade-left" data-aos-easing="ease-out-sine" data-aos-duration={delay+0}>
				Passionite about coding, designing, and learning more efficient ways to solve problems.
			</div>
		</div>
	)
	const bio2 = () => (
		<div>
			<div data-aos="fade-left" data-aos-easing="ease-out-sine" data-aos-duration={delay+900}>
				I grew up in a suburb of Chicago in a Korean family. My school days before University were not very productive<br/>
				as a lot of the time I should've spent studying were instead devoted towards playing games with friends or playing the cello.<br/>
			</div>
			<div style={{marginTop:'.5em'}} data-aos="fade-left" data-aos-easing="ease-out-sine" data-aos-duration={delay+600}>
				It wasn't until the end of Junior year that I had realized that I really need to pay attention to my future.<br/>
				I studied my brain out for the ACT and pulled myself together for the rest of my time as a high schooler.<br/>
				I decided on Computer Science as my future on a whim which could have gone really bad now that I think about it... but it worked out really well.<br/>
			</div>
			<div style={{marginTop:'.5em'}} data-aos="fade-left" data-aos-easing="ease-out-sine" data-aos-duration={delay+300}>
				I met some really cool people in University and it was there where my passion for coding and designing was born.<br/>
				I got to work on, create, and collaborate in many different projects and learned the most when working with others.<br/> 
			</div>
			<div style={{marginTop:'.5em'}} data-aos="fade-left" data-aos-easing="ease-out-sine" data-aos-duration={delay+0}>
				While I do enjoy programming and creating projects in my free time, I still enjoy playing games from time to time<br/>
				and have even entered a couple of Tetris tournaments where I won some money.<br/>
				Music continues to be a part of my life through University where I learned to play the piano.<br/>
				I enjoy learning new pieces and rewriting songs for the piano.<br/>
			</div>
		</div>
	)
	return (
		<div
		style={{
			overflow:'hidden',
			position:"absolute",
			width:'100%',
			height:'100%',
			color: '#fff',
			fontSize: '1em',
			textDecoration: 'none',
			fontFamily: 'Dosis, sans-serif',
		}}>
			{image2()}
			<div style={{marginLeft: '2.25em', marginTop: '0%'}}>
				<div
				data-aos="fade-right" data-aos-easing="ease-out" data-aos-duration="1000"
				style={{marginTop: '2.25em', position: 'absolute'}}
				>
					{image1()}
				</div>
				<div
				style={{
					marginLeft: '20vw',
					marginTop:'1em',
					fontSize: '8vh',
					fontWeight: 'bold',
				}}
				>
					<div data-aos="fade-in" data-aos-easing="ease-out" data-aos-duration="5000">
						<div style={{float:'left', marginRight:'3vh'}}>
							About Me<br/>
						</div>
					</div>
				</div>
			</div>
			<div
			style={{
				width:'100%',
				display: 'inline-block',
				marginTop:'.5em',
				marginLeft:'1em',
				fontSize: '7vh',
			}}>
				<div style={{fontSize: '.5em'}} data-aos="fade-in" data-aos-easing="ease-out-sine" data-aos-duration={delay+500}>
					<table style={{opacity:'.9', marginLeft: 'auto', marginRight: 'auto'}} data-aos="fade-right" data-aos-easing="ease-out-sine" data-aos-duration={delay+500}>
						<thead style={{margin:'1em', fontWeight: 'bold'}}>
							<tr>
								<th>Languages</th>
								<th>Frameworks</th>
								<th>Databases</th>
							</tr>
						</thead>
						<tr><td>Java</td>		<td>React</td>		<td>PostgreSQL</td>	</tr>
						<tr><td>JavaScript</td>	<td>Spring</td>		<td>MySQL</td>		</tr>
						<tr><td>C</td>			<td>ASP.NET</td>	<td id="noCursor"/>	</tr>
						<tr><td>Ruby</td>		<td id="noCursor"/>		<td id="noCursor"/>	</tr>
						{/* <tr><td>Ruby</td>		<td>Rails</td>		<td id="noCursor"/>	</tr> */}
						<tr><td>Python</td>		<td id="noCursor"/>	<td id="noCursor"/>	</tr>
						<tr><td>C#</td>			<td id="noCursor"/>	<td id="noCursor"/>	</tr>
						<tr><td>C++</td>		<td id="noCursor"/>	<td id="noCursor"/>	</tr>
					</table>
				</div>
			</div>
			<div style={{position:'relative', textAlign:"center", margin:".1em"}} data-aos="fade-in" data-aos-easing="ease-out-sine" data-aos-duration={delay+1200}>
				<div style={{display:'inline-block'}}>
					<div style={{float:'left', cursor:'pointer'}} onClick={() => updatePersonal(!personal)}>
						<div className='personalToggle'>
							{!personal ? 'click for personal bio' : 'click for default bio'}
						</div>
					</div>
				</div>
			</div>
			<div className='aboutDescription' data-aos="fade-in" data-aos-easing="ease-out-sine" data-aos-duration={delay+1200}>
				<div className='aboutDescriptionContent'>
					{!personal
						? bio1()
						: bio2()
					}
				</div>
			</div>
		</div>
	)
}
export default About;